.adminContainer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  position: relative;
  top: 8rem;
}

.adminSection {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.adminSection h2 {
  color: white;
  font-size: 3.5rem;
  text-align: center;
}

.adminWelcome {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 4rem;
}

.logoAdmin {
  width: 60%;
}
.topNav {
  width: 100%;
  display: flex;
  grid-gap: 43rem;
  position: fixed;
  padding: 1.5rem;
  left: 5rem;

  justify-content: center;
}

.topNavName {
  font-size: 2rem;
  color: #7d7171;
  font-weight: bolder;
}

.outAdmin {
  color: #ce73ff;
  font-size: 2.5rem;
}
